html {
  --font-color: #00acdc;
  --font-family: "Roboto", sans-serif;
}

body {
  background-color: #e7e5e5;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img-link {
  display: block;
}

.header-img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  box-sizing: border-box;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-item {
  margin-left: 20px;
}

.nav-list :first-child {
  margin-left: 0;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav-link:hover {
  color: var(--font-color);
}

.is-active {
  color: var(--font-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--font-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

